.about{
    width: 80%;
    margin: auto;
    /* display: flex; */
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    height: 100%;
    
}
.about h2{
    text-align: center;
    color: #fff;
}
.description{
    margin: 3rem 0 3rem 0;
    line-height: 2rem;
}

.about_image_section{
    display:flex;
    align-items: center;
    justify-content: space-between;
    margin: 20px auto;
}
.rajesh_image img{
    height:"200px";
    width:"180px";
    border-radius:"20px"
}

@media screen and (max-width:640px){
    body{
        margin:0px;
        padding:0px;
    }
    .about_image_section{
        display: grid;
        grid-template-columns: 1fr;
        align-items: center;
        justify-content: center;
        margin: auto;
        /* border:2px solid red */
    }
    .rajesh_image img{
        display:flex;
        align-items: center;
        text-align: center;
        margin:auto;
        margin-bottom: 50px;

    }
    .description{
        margin: -3rem 0 3rem 0;
        line-height: 2rem;
    }
    .description p{
            text-align:justify;
    }
}