.stats{
    width: 100%;
    margin:  auto;
    /* border:1px solid white; */
}

.stats-part1{
    width: 100%;
    display: flex;
    text-align: center;
    margin:2rem auto;
   /* border:1px solid white; */
    gap:2rem;
}

.stats-language{
    text-align: center;
    margin-bottom: 2rem;
}

@media screen and (max-width:1044px){
    .stats-part1{

        width: auto;
        display: grid;
        grid-template-columns: repeat(1,1fr);
        text-align: center;
        margin: auto;
        /* border:2px solid red */
    }
    .stats-part1 img{
        width:100%;
        align-items: center;
    }
    .stats-language img{
        width:100%;
        height:150px;
        text-align: center;
    }
}