.footer{
    width:100%;
    padding: 6rem 0;
    background-color: rgba(19,19,19,0.8);
}
.footer-container{
    max-width: 90%;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 40px;
}

.left{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}

.location ,.phone, .email{
    display: flex;
    margin: 0.8rem;
}

.right{
    height:100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
}
.social{
    margin-top: 1rem;
}
h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}
p{
    font-size: 1.2rem;
}

@media screen and (max-width:740px){
    .footer-container{
        display: grid;
        grid-template-columns: 1fr;
        /* border:2px solid red */
    }
    .left{
        height: 100%;
        width: 80%;
        display: flex;
        flex-direction: column;
        padding: 1rem;
        box-sizing: border-box;
    }
    .location ,.phone, .email{
        display: flex;
        margin-top: 2px;
    }
    .copyright{
        width:80%;
        margin:auto;
    }
}